@import url(https://use.fontawesome.com/releases/v5.15.4/css/all.css);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;1,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300;700&display=swap);
@media (max-width: 950px) {
  header {
    flex-direction: column;
  }
  .homePage {
    flex-direction: column;
    align-items: center;
  }
  .home-text-box {
    font-size: 30px;
    margin-top: 120px;
  }
  .details-text {
    max-width: 50%;
    font-size: 10px;
  }
  .details-info-img img {
    max-width: 70%;
    min-height: 50%;
  }
  #comment-area {
    width: 80%;
    margin: auto;
  }
  #inp-name {
    width: 50%;
    margin: auto;
  }
  .comment-container {
    max-width: 90%;
    margin: auto;
  }
  .my-msg-stra {
    max-width: 90%;
  }
}

/* MEDIA  600px */
@media (max-width: 600px) {
  header {
    flex-direction: column;
  }
  .homePage {
    flex-direction: column;
    align-items: center;
  }
  .homePage-img {
    min-width: 100%;
  }
  .home-message {
    min-width: 100%;
  }
  .home-text-box {
    font-size: 30px;
    margin-top: 120px;
  }
  .footer {
    flex-direction: column;
    min-height: 4.5rem;
  }
  .catalog {
    padding-bottom: 40px;
  }
  /* catalog */
  .cards .card {
    width: 140px;
  }
  .cards .card .img img {
    width: 80px;
    height: 100px;
  }

  /* Details */
  .details {
    min-width: 100%;
  }
  .details-info {
    flex-direction: column;
  }
  .details-info-img {
    min-width: 100%;
  }
  .details-info-img img {
    min-width: 100%;
    margin: auto;
  }
  .details-text {
    max-width: 100%;
    font-size: 20px;
  }
  .article-info {
    flex-direction: column;
  }
  .art-info-items {
    min-width: 100%;
  }
  .message-box {
    min-width: 90%;
  }
  #root > section > section > article.article-info > article > form {
    margin: auto;
  }
  /* Profile */

  .profile {
    flex-direction: column;
  }
  .profile-img-liks-section {
    min-width: 90%;
  }
  .profile-img-section img {
    width: 110px;
  }
  .profile-info {
    min-width: 90%;
  }
  /* Messages */
  .msg-box-content {
    flex-wrap: wrap;
  }
  .message-conrainer {
    min-width: 100%;
    height: 60ch;
  }
  .msg-box-message-content {
    font-size: 0.7em;
  }
  .msg-box {
    min-width: 90%;
    font-size: 1.6em;
  }
  .chat-msg-box,
  .chat-msg-box-me {
    min-width: 100%;
  }
  #txt-area {
    font-size: 1.5em;
  }

  .discussion-container {
    flex-direction: column;
  }
  .discussion-article-info-img {
    min-width: 100%;
  }
  .discussion-art-img {
    width: 100%;
  }
  .discussion-art-img img {
    max-width: 150px;
    max-height: 150px;
  }
  .chat-msg-container {
    min-width: 100%;
  }
  .del-discuss-btn {
    margin-bottom: 20px;
  }
  /* create , edit*/
  .create-article,
  .edit-article {
    min-width: 100%;
  }
  .create-article-box form,
  .edit-article-box form {
    min-width: 80%;
  }

  /*login /register*/
  .login-box,
  .register-box {
    min-width: 100%;
  }
  .login-form,
  .register-form {
    min-width: 100%;
  }
  .register-box .register-untertitle,
  .signin,
  .signup,
  .login-untertitle,
  .login-form label,
  .login-form input,
  .register-form label,
  .register-form input,
  .create-article-box .create-art-untertitle,
  .create-article-box form label,
  .edit-article-box form label {
    font-size: large;
  }
}

@media screen and (orientation: landscape) {
  #hm-txt-bx {
    position: static;
    transform: none;
  }
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

body,
html {
  margin: 3px;
  position: relative;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  background-color: white;
}
.container {
  padding-bottom: 35px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #b9e3f7;
  border-radius: 5px;
}
.nav {
  display: flex;
}
li {
  list-style: none;
  margin: 5px 10px;
  font-weight: bold;
}

li a {
  text-decoration: none;
  color: #3f82ec;
  padding-right: 10px;
}
.logOutBtn {
  border: none;
  background-color: #b9e3f7;
  color: #3f82ec;
  font-weight: bolder;
  font-size: 1em;
}
.logOutBtn:hover {
  color: tomato;
}

.logo i {
  font-size: 50px;
  margin-left: 10px;
  color: #3f82ec;
}
.logo {
  display: flex;
  align-items: center;
}
.logo i:hover {
  font-size: 55px;
}
header li a:hover {
  font-size: larger;
  color: tomato;
}

/* Dropdown Menu */

.dropbtn {
  color: white;
  background-color: #3f82ec;
  width: 100px;
  height: 25px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
}

.dropbtn i.fas {
  font-size: 17px;
  color: white;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  border-radius: 8px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  font-weight: bold;
  padding: 5px 8px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {
  background-color: rgb(114, 167, 228);
}

.dropdown:hover .dropdown-content {
  display: block;
}

.userName {
  text-transform: capitalize;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
  border-radius: 5px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #b9e3f7;
}
.footer-nav-items {
  display: flex;
  margin: 5px;
}
.footer-title {
  font-size: 1.2em;
  margin: 5px;
  color: #3f82ec;
  font-style: italic;
}
.footer-nav-items li {
  padding-left: 10px;
}

.homePage {
  display: flex;
}
.homePage-img {
  max-width: 50%;
}
.homePage-img img {
  width: 100%;
}
.home-message {
  width: 50%;
  color: #7fabf1;
  font-size: 3em;
  position: relative;
}
.home-text-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.card {
  max-width: 200px;
  text-align: center;
  border-radius: 10px;
  margin: 10px;
  background-color: #4a82cb;
  padding: 10px 10px;
  color: #fdfefa;
  box-shadow: -1px 1px 8px 3px #000000;
  display: flex;
  flex-direction: column;
  margin: 20px;
}
.img {
  max-width: 100%;
  padding: 10px;
}
.img img {
  width: 150px;
  height: 150px;
  border-radius: 30px;
}
.info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.card-info {
  width: 50%;
  font-weight: bold;
  font-family: Helvetica, sans-serif;
}

.details-btn {
  width: 100%;
  text-decoration: none;
  color: oldlace;
  font-weight: bold;
}
.details-btn:hover {
  color: rgb(236, 182, 3);
}
.likes i {
  color: rgb(199, 13, 13);
  font-size: 1.2em;
}
.price {
  color: #95a681;
}

.catalog {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.catalog-title {
  flex-basis: 100%;
  font-size: 2em;
  font-weight: bolder;
  text-align: center;
  border-bottom: 2px solid #3f82ec;
  color: #3f82ec;
  padding-bottom: 20px;
  margin: 10px auto;
}

.sv-msg {
  font-size: 1.5em;
  
}

.profile {
  display: flex;
  background-color: #b9e3f7;
  justify-content: space-between;
  font-family: Helvetica, sans-serif;
}
.profile-img-liks-section {
  margin: 5px auto;
  width: 25%;
  background-color: #333333;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.profile-img-section {
  max-width: 100%;
}
.profile-img-section img {
  border-radius: 100px;
  max-width: 150px;
  margin: 20px;
}
.nav-links {
  padding: 0;
}
.nav-links li {
  padding-bottom: 7px;
}
.nav-links a {
  color: #c2dd9e;
  font-size: large;
}
.profile-links-section {
  margin-top: 20px;
}
.uploadImg form {
  display: flex;
  flex-wrap: wrap;
  color: rgb(224, 146, 159);
  text-align: center;
}
.uploadImg form label {
  flex-basis: 100%;
}
.uploadImg #img {
  width: 50%;
  border-radius: 10px;
}
.uploadImg .submitbtn {
  padding: 1 3px;
  width: 95px;
  margin: auto;
}
.uploadImg .submitbtn:hover {
  background-color: #a6f4ff;
}

.profile-info {
  width: 70%;
  background-color: floralwhite;
  border-radius: 5px;
  margin: 5px auto;
}
.user-info {
  padding: 0 20px;
  font-size: large;
}

.user-info p {
  border-bottom: 2px solid lightgray;
  padding-bottom: 8px;
}
.user-info-title {
  text-align: center;
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #626d53;
}

.user-info p {
  display: flex;
  justify-content: space-between;
}
.user-info p .data-span {
  padding-right: 100px;
  color: #3f82ec;
}
.user-info-name strong {
  text-transform: capitalize;
}
.profile-info-article {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  overflow: scroll;
  height: 400px;
}

.profile-article-title {
  font: italic small-caps bold 12px/30px Georgia, serif;
  flex-basis: 100%;
  text-align: center;
  font-size: 1.5em;
  color: #626d53;
}
.no-liked-art {
  margin: auto;
  font: italic small-caps bold 12px/30px Georgia, serif;
  color: #626d53;
}

.msg-container {
  margin-bottom: 5px;
}
.msg-box {
  max-width: 70%;
  margin: auto;
  border: 1px solid black;
  background-color: cornsilk;
  border-radius: 6px;
}
.msg-box-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid black;
}
.msg-box-img {
  max-width: 25%;
  padding: 5px;
}
.msg-box-img img {
  width: 70px;
  height: 60px;
  border-radius: 5px;
}
.msg-box-message {
  max-width: 80%;
  margin: auto;
}
.msg-box-message-content {
  margin: 5px auto;
  width: 50%;
  white-space: nowrap;
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.time-author {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid black;
}
.msg-box-author,
.msg-time {
  margin: 3px 10px;
  font-size: 15px;
  font-style: italic;
  color: blue;
}

.msg-card-btn {
  background-color: rgb(86, 185, 86);
  padding: 0 9px;
  border-radius: 5px;
  margin-right: 10px;
}
.msg-card-btn a {
  color: whitesmoke;
  font-size: 0.8em;
  text-decoration: none;
  font-style: italic;
}

.message-conrainer {
  min-height: 100vh;
  background-color: #b9e3f7;
  max-width: 80%;
  margin: 20px auto;
  border: 2px solid lightgray;
  border-radius: 5px;
  box-shadow: -1px 1px 8px 3px #000000;
  position: relative;
}
.message-container-title {
  text-align: center;
  font-family: Georgia, "Times New Roman", Times, serif;
  border-bottom: 2px solid black;
  padding-bottom: 10px;
}
.sv-msg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-style: italic;
  color: blue;
  font-size: 2em;
}

.chat-msg-box-me {
